export default {
    data() {
        return {
        }
    },
    methods: {
        getLatLngsData() {
            return Object.keys(latlngs).map(l => ({
                _nameabbrevation: l,
                _areaCode: districtCode[l],
                _latlng: latlngs[l],
                _latlngFullScreen: latlngsFullScreen[l],
            }))
        },
        initMapArea(){
            // 绘制 城区区块
            const { strokeNormalColor, fillNormalOpacity } = districtPolygonStyle
            if(gis_hangzhou){
                this.gis_hangzhou = gis_hangzhou.child.map((c,i) =>{
                    const bounds = [toPolygonPoint(c.points[0].region)]

                    const polygon = L.polygon(bounds, {
                        color: strokeNormalColor,
                        weight: 2,
                        fillOpacity: fillNormalOpacity
                    }).addTo(this.mapArea)

                    this.latlngsData = this.getLatLngsData()
                    const polygonData = this.latlngsData.find(l => l._nameabbrevation === c.nameabbrevation)
                    if (polygonData) {
                        Object.keys(polygonData).forEach(p => {
                            polygon[p] = polygonData[p]
                        })
                    }
                    return polygon
                })
            }
        }
    },
    mounted() {
    }
}