            switchPolygonOpacity(tar) {
                // 点击切换城区 polygon 透明度
                const {_areaCode} = tar
                const polygon = this.gis_hangzhou.find(g => g._areaCode === _areaCode)
                const { fillNormalOpacity, fillHoverOpacity, strokeNormalColor, strokeHoverColor } = districtPolygonStyle
                this.currentPolygon && this.currentPolygon.setStyle({
                    fillOpacity: fillNormalOpacity,
                    color: strokeNormalColor
                })
                if (polygon) {
                    polygon.setStyle({
                        fillOpacity: fillHoverOpacity,
                        color: strokeHoverColor
                    })
                    this.currentPolygon = polygon
                } else {
                    this.currentPolygon = null
                }
            },
        },
